#card {
    width: 20rem;
    border: 1px solid;
    box-shadow: rgb(131 131 131) 3px 12px 14px 0px;
    height: 25 rem;
    width: 20 rem;
}

#header {
    height: 5rem;
    padding: 1rem;
    box-shadow: rgb(174, 206, 210) 1px -1px 20px 6px inset;
    border-bottom: 1px solid black;
}

#body {
    box-shadow: rgb(210, 174, 174) 1px -1px 20px 6px inset;
}

.darkcard {
    width: 20rem;
    border: 1px solid white;
    box-shadow: rgb(131 131 131) 3px 12px 14px 0px;
    height: 25rem;
    width: 20rem;
    background-color: black;
    color: wheat;
}

.darkheader {
    height: 5rem;
    padding: 1rem;
    border: 1px solid white;
    /* box-shadow: rgb(174, 206, 210) 1px -1px 20px 6px inset; */
    border-bottom: 1px solid black;
}

.darkbody {
    border: 1px solid white;
    /* box-shadow: rgb(210, 174, 174) 1px -1px 20px 6px inset; */
}